import Vue from 'vue';
import Component from 'vue-class-component';
import { Action } from 'vuex-class';

@Component({})
export default class SettingComp extends Vue {

    @Action('showToast')
    showToast;

    toPage(name: string) {
        this['$router'].push({
            name
        });
    }
}



